// src/app/page.tsx
'use client';

import { useEffect, useMemo, useState } from 'react';
import { useAccount, useChainId, useReadContract, useWriteContract, useWaitForTransactionReceipt } from 'wagmi';
import { base } from 'wagmi/chains';
import { ConnectButton } from '@rainbow-me/rainbowkit';
import { formatUnits } from 'viem';
import { COLLECTION_ABI } from '@/lib/abi/collection';
import { ERC20_ABI } from '@/lib/abi/erc20';
import { CHAIN_ID, CONTRACT_ADDRESS, USDC_ADDRESS, USDC_DECIMALS, MAX_QTY_UI } from '@/lib/constants';

export default function Home() {
  const chainId = useChainId();
  const { address, isConnected } = useAccount();

  const [qty, setQty] = useState<number>(1);

  // --- Reads ---
  const { data: remaining }   = useReadContract({ address: CONTRACT_ADDRESS, abi: COLLECTION_ABI, functionName: 'remaining' });
  const { data: totalSupply } = useReadContract({ address: CONTRACT_ADDRESS, abi: COLLECTION_ABI, functionName: 'totalSupply' });
  const { data: maxSupply }   = useReadContract({ address: CONTRACT_ADDRESS, abi: COLLECTION_ABI, functionName: 'maxSupply' });
  const { data: priceUSDC }   = useReadContract({ address: CONTRACT_ADDRESS, abi: COLLECTION_ABI, functionName: 'priceUSDC' });
  const { data: saleActive }  = useReadContract({ address: CONTRACT_ADDRESS, abi: COLLECTION_ABI, functionName: 'saleActive' });
  const { data: maxPerWallet }= useReadContract({ address: CONTRACT_ADDRESS, abi: COLLECTION_ABI, functionName: 'maxPerWallet' });
  const { data: mintedOfMe }  = useReadContract({
    address: CONTRACT_ADDRESS, abi: COLLECTION_ABI, functionName: 'mintedOf',
    args: [address ?? '0x0000000000000000000000000000000000000000']
  });

  const { data: allowance }   = useReadContract({
    address: USDC_ADDRESS, abi: ERC20_ABI, functionName: 'allowance',
    args: [address ?? '0x0000000000000000000000000000000000000000', CONTRACT_ADDRESS]
  });

  // --- Derived values ---
  const requiredAmount: bigint = useMemo(() => {
    const p = (priceUSDC ?? 0n);
    return p * BigInt(qty);
  }, [priceUSDC, qty]);

  const hasEnoughAllowance = useMemo(() => {
    if (allowance === undefined) return false;
    return allowance >= requiredAmount;
  }, [allowance, requiredAmount]);

  const walletOverLimit = useMemo(() => {
    if (!maxPerWallet || !mintedOfMe) return false;
    return (mintedOfMe as bigint) + BigInt(qty) > (maxPerWallet as bigint);
  }, [maxPerWallet, mintedOfMe, qty]);

  const guardReason = useMemo(() => {
    if (!isConnected) return 'Connect wallet to continue';
    if (chainId !== CHAIN_ID) return 'Switch to Base mainnet';
    if (saleActive === false) return 'Sale is not active';
    if ((remaining ?? 0n) === 0n) return 'Sold out';
    if (qty < 1 || qty > MAX_QTY_UI) return `Qty must be 1–${MAX_QTY_UI}`;
    if (walletOverLimit) return 'Exceeds wallet limit';
    return null;
  }, [isConnected, chainId, saleActive, remaining, qty, walletOverLimit]);

  // --- Writes ---
  const { writeContract: write, data: txHash, isPending } = useWriteContract();
  const { data: receipt, isLoading: isConfirming } = useWaitForTransactionReceipt({ hash: txHash });

  const approve = async () => {
    if (!requiredAmount) return;
    write({
      chainId: base.id,
      address: USDC_ADDRESS,
      abi: ERC20_ABI,
      functionName: 'approve',
      args: [CONTRACT_ADDRESS, requiredAmount],
    });
  };

  const mint = async () => {
    write({
      chainId: base.id,
      address: CONTRACT_ADDRESS,
      abi: COLLECTION_ABI,
      functionName: 'mint',
      args: [BigInt(qty)],
    });
  };

  // Simple UI helpers
  const fmt = (v?: bigint) => v === undefined ? '—' : formatUnits(v, USDC_DECIMALS);
  const disabledMint = !!guardReason || !hasEnoughAllowance || isPending || isConfirming;
  const disabledApprove = !!guardReason || hasEnoughAllowance || isPending || isConfirming;

  useEffect(() => {
    // Clamp qty to remaining if smaller than MAX_QTY_UI
    if (remaining !== undefined) {
      const maxByRemaining = Number(remaining > BigInt(MAX_QTY_UI) ? MAX_QTY_UI : remaining);
      if (qty > maxByRemaining) setQty(Math.max(1, maxByRemaining));
    }
  }, [remaining]);

  return (
    <main className="min-h-screen bg-neutral-950 text-white">
      <header className="flex items-center justify-between p-4 border-b border-neutral-800">
        <div className="text-lg font-semibold">Quantum Mints</div>
        <ConnectButton />
      </header>

      <section className="max-w-3xl mx-auto p-6 grid gap-6">
        {/* Status Card */}
        <div className="rounded-2xl border border-neutral-800 bg-neutral-900/60 p-6">
          <h2 className="text-xl font-bold mb-4">Collection Status</h2>
          <div className="grid grid-cols-2 md:grid-cols-3 gap-4 text-sm">
            <Stat label="Remaining" value={remaining?.toString() ?? '—'} />
            <Stat label="Total Supply" value={totalSupply?.toString() ?? '—'} />
            <Stat label="Max Supply" value={maxSupply?.toString() ?? '—'} />
            <Stat label="Price (USDC)" value={fmt(priceUSDC as bigint)} />
            <Stat label="Max / Wallet" value={maxPerWallet?.toString() ?? '—'} />
            <Stat label="Minted by You" value={(mintedOfMe as bigint|undefined)?.toString() ?? (isConnected ? '0' : '—')} />
          </div>
          {guardReason && (
            <p className="mt-3 text-amber-300 text-sm">⚠️ {guardReason}</p>
          )}
        </div>

        {/* Controls */}
        <div className="rounded-2xl border border-neutral-800 bg-neutral-900/60 p-6">
          <h2 className="text-xl font-bold mb-4">Mint</h2>
          <div className="flex items-center gap-3">
            <label className="text-sm text-neutral-300">Quantity</label>
            <input
              type="number"
              min={1}
              max={MAX_QTY_UI}
              value={qty}
              onChange={(e) => setQty(Math.min(MAX_QTY_UI, Math.max(1, Number(e.target.value || 1))))}
              className="w-20 bg-neutral-800 border border-neutral-700 rounded-lg px-3 py-2 outline-none"
            />
            <div className="ml-auto text-sm text-neutral-400">
              Cost: <span className="text-white font-mono">{fmt(requiredAmount)}</span> USDC
            </div>
          </div>

          <div className="mt-4 flex flex-wrap gap-3">
            <button
              onClick={approve}
              disabled={disabledApprove}
              className={`px-4 py-2 rounded-lg border ${disabledApprove ? 'opacity-50 cursor-not-allowed' : 'hover:opacity-90'} border-cyan-300/30 bg-cyan-300/20`}
            >
              {isPending ? 'Pending…' : 'Approve USDC'}
            </button>
            <button
              onClick={mint}
              disabled={disabledMint}
              className={`px-4 py-2 rounded-lg border ${disabledMint ? 'opacity-50 cursor-not-allowed' : 'hover:opacity-90'} border-emerald-300/30 bg-emerald-300/20`}
            >
              {isConfirming ? 'Confirming…' : 'Mint'}
            </button>
          </div>

          {!hasEnoughAllowance && !guardReason && (
            <p className="mt-3 text-sm text-neutral-300">
              You need to approve <span className="font-mono">{fmt(requiredAmount)}</span> USDC before minting.
            </p>
          )}
        </div>

        <FooterNote />
      </section>
    </main>
  );
}

function Stat({ label, value }: { label: string; value: string }) {
  return (
    <div className="rounded-xl border border-neutral-800 bg-neutral-900/40 p-3">
      <div className="text-neutral-400 text-xs">{label}</div>
      <div className="font-mono">{value}</div>
    </div>
  );
}

function FooterNote() {
  return (
    <p className="text-xs text-neutral-500">
      Network: Base (8453). Currency: USDC (6 decimals). Contract: {CONTRACT_ADDRESS.slice(0,6)}…{CONTRACT_ADDRESS.slice(-4)}
    </p>
  );
}
